.Home {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  background-color: #f3e8e0;

  &__title {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    width: 100%;

    &__container {
      width: 75%;
      min-width: 350px;
      display: flex; 
      flex-direction: column;
      align-items: flex-end;
      position: relative;

      .svg {
        z-index: 1;
        width: 100%;
        filter: 
          invert(42%) 
          sepia(93%) 
          saturate(1500%) 
          hue-rotate(340deg) 
          brightness(90%) 
          contrast(119%);
      }

      .image-container {
        width: 28%;
        position: absolute;
        right: 5%;
        top: -3%;
        background: rgb(202, 58, 21);
        border-radius: 50%; 
        overflow: hidden;
        
        
        img {
          height: 100%;
          width: 100%;
          mix-blend-mode: screen;
        }
      }
    }
  }

  &__main {
    width: max(350px, 70%);
    font-family: $paragraphFont;
    color: $home-color-2;

    &__paragraph {
      font-size: clamp(1.6rem, 1.3333rem + 1.1852vw, 2.4rem);
      margin-bottom: 1.5rem;
    }
  }
}

